body {
  font-family: 'Roboto', sans-serif;
}

.page-header {
  padding: 30px;
  background: #2196f3;
  color: #fff;
  text-align: center;
  margin-bottom: 30px;
}

.text-red {
  color: #f44336;
}

.text-green {
  color: #4caf50;
}

.app_header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 30px;
}

.app_stats {
  margin-bottom: 30px;
}

.card {
  padding: 15px;
  background: #fff;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.1);
}

.card.total-cases h3 {
  color: #03a9f4;
}

.card.total-recovered h3 {
  color: #4caf50;
}

.card.total-deaths h3 {
  color: #f44336;
}

.table td {
  padding: 5px .75rem;
}

.table-striped tbody tr:nth-of-type(odd) {
  background-color: rgba(0, 0, 0, .02);
}

table tr td:nth-child(-n+2) {
  vertical-align: middle;
}

table tr td:nth-child(n+3) {
  vertical-align: bottom;
}